//  Context , value     
import { createContext, useState } from "react";
import Hospital from '../json/Hospital.json'

const DataContext = createContext();

// DataProvider    value    
const DataProvider = ({ children }) => {
    //
    const [infant, setInfant] = useState({ name: "", age: "2021-01-24", gender: "" })
    const [measures, setMesures] = useState({
        height: 0,
        weight: 0, temperature: 0, medicine: ""
    })
    const [ismeasures, setIsMeasures] = useState(false)
    const [login, setLogin] = useState(true)
    const [hcheck, setHcheck] = useState(0);
    const [icheck, setIcheck] = useState(0);
    const age = String(infant.age)
    const date1 = new Date(infant.age);
    const date2 = new Date();
    const diffDate = date1.getTime() - date2.getTime();
    const date = Math.floor(Math.abs(diffDate / (1000 * 60 * 60 * 24)));
    const month = Math.floor(Math.abs(diffDate / (1000 * 60 * 60 * 24 * 30)));
    //  
    const [category, setCategory] = useState();
    const [hospitalData, setHospitalData] = useState(Hospital);
    // 
    //   
    //     
    // >> id/name/age/gender/questionnaire{}/medicine{}/
    const [user, setUser] = useState([
        {
            id: 1,
            userId: "seo",
            userPw: "123",
            username: "",
            age: 20,
            gender: "F",
            questionnaire: {
                weight: 50,
                height: 160,
            },
            medicine: {
                cough: true,
                fever: false,
            },
        },

    ]);


    //    state
    const [loginUser, setLoginUser] = useState(
        {
            id: 1,
            userId: "seo",
            userPw: "123",
            username: "",
            age: 20,
            gender: "F",
            questionnaire: {
                weight: 50,
                height: 160,
            },
            medicine: {
                cough: true,
                fever: false,
            },
        }
    );


    // 
    // 
    const [comments, setComments] = useState([
        {
            Id: 1,
            name: "user",
            countStar: 3,
            btn1: "",
            btn2: "",
            btn3: "",
            review: "aaaaaaaaaaaaaaaaaaaaaaaaaaaaa",
            yesNo: ""
        }
    ]);

    const [commentCount, setCommentCount] = useState(2);

    //  value state() action()  
    const value =
    {
        state: { user, loginUser, comments, commentCount,infant, login, hcheck, icheck, date, month, age, measures, ismeasures,category,hospitalData },
        action: { setUser, setLoginUser,setInfant, setLogin, setHcheck, setIcheck, setMesures, setIsMeasures ,setCategory,setHospitalData}
    };

    // DataProvider  , DataContext.Provider  
    // , {children} Provider   
    return (
        <DataContext.Provider value={value}>
            {children}
        </DataContext.Provider>
    );
};


// // consumer 
// // DataContext   , DataConsumer 
const { Consumer: DataConsumer } = DataContext;

// //    export > .Provider  
// //  Provider App   
export { DataProvider, DataConsumer }

//      export
export default DataContext;