import { useSelector } from 'react-redux';
import { useContext} from "react";
import DataContext from "../data/DataContext";
import Preview from './Preview';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import '../css/Notice.css'
import { useState, useEffect } from 'react';
import Notice_Modal from './Notice_Modal';

const Notice = () => {
    const preview4 = useSelector((state)=>(state.temperature.preview4))

    const data = useContext(DataContext);
    const [show,setShow] = useState(false);
    const [name,setName] = useState("");
    const temp = data.state.measures.temperature;
    const fever = function() {
        if(temp >= 39 ){
            setName("")
        } else if(temp >= 37.5 ){
            setName("")
        } else if(temp >= 35.5){
            setName("")
        }    
    }
    useEffect(() => {
        fever()
    },[temp])
    return (  
        <div className='first_box'>
        {data.state.measures.temperature ? (
            <>
                {data.state.measures.temperature}C
                <p>(): {name} </p>
                {name == "" ? 
                (
                    <>
                        <p>{name}     </p>
                        <ul>
                            <li>{name} . </li>
                            <li> ,      ...</li>
                            <li>   </li>
                            <li>     ...</li>
                        </ul>
                    </>
                ):(
                    <>
                        <p>{name}     </p>
                        <ul>
                            <li>{name} . 1   ...</li>
                            <li> ,      ...</li>
                            <li>   </li>
                            <li>     ...</li>
                        </ul>
                    </>
                )}
               <button className='plus_btn' onClick={()=>{
                    setShow(true)
                }}><FontAwesomeIcon icon={faPlus} /></button>
                {show && <Notice_Modal setShow={setShow} />}
            </>
        ):(
            data.state.login ? (
                <>
                    <div className='My_row'>
                        <ul>
                            <li className='title'>{data.state.infant.name}  </li>
                            <li>  </li>
                            <li><strong className='bold' >  </strong>   </li>
                        </ul>
                        <button className='plus_btn' onClick={()=>{
                            setShow(true)
                        }}><FontAwesomeIcon icon={faPlus} /></button>
                        {show && <Notice_Modal setShow={setShow} />}
                </div>  
                </>
            ):(
                <>
                    <Preview content={preview4} />
                </>
            )
        )}
        </div>
    );
}
 
export default Notice;